:root {
  --cyan-main: hsl(172, 67%, 45%);
  --cyan-dark: hsl(183, 100%, 15%);
  --cyan-d-gray-1: hsl(186, 14%, 43%);
  --cyan-d-gray-2: hsl(184, 14%, 56%);
  --cyan-l-gray-1: hsl(185, 41%, 84%);
  --cyan-l-gray-2: hsl(189, 41%, 97%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-mono: "Space Mono", monospace;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-mono);
  background-color: var(--cyan-l-gray-1);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.main {
  background-color: var(--white);
  padding: 2rem;
  border-radius: 1.5rem;
  margin-top: -1rem;
  width: 100%;
}
@media (min-width: 64em) {
  .main {
    width: 80%;
    display: flex;
    justify-content: space-between;
  }
  .main .output {
    margin-left: 2rem;
  }
}

.input {
  width: 100%;
}
.input__wrapper {
  margin-bottom: 1.9rem;
}
.input__form {
  position: relative;
  margin: 0.5rem 0 0.5rem 0;
}
.input__label {
  font-size: 1rem;
  font-weight: 700;
  color: var(--cyan-d-gray-2);
}
.input__text {
  background-color: var(--cyan-l-gray-2);
  border: none;
  width: 100%;
  border-radius: 0.5rem;
  color: var(--cyan-dark);
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: 700;
  text-align: right;
  padding: 0.4rem 1rem;
  -webkit-appearance: none;
  -moz-appearance: textfield;
}
.input__text:focus {
  outline: 2px solid var(--cyan-main);
}
.input__img {
  position: absolute;
  left: 1.1rem;
  top: 30%;
}
.input__tipbox {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 1rem;
  row-gap: 1rem;
  margin-top: 1rem;
}
.input__btn {
  background-color: var(--cyan-dark);
  color: var(--cyan-l-gray-2);
  border: none;
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: 700;
  padding: 0.4rem 3.2rem;
  border-radius: 0.5rem;
  width: 100%;
}
.input__btn-reverse {
  background-color: var(--cyan-main);
  color: var(--cyan-dark);
  text-transform: uppercase;
}
.input__active {
  background-color: var(--cyan-main);
  color: var(--cyan-dark);
}

.output {
  background-color: var(--cyan-dark);
  border-radius: 1rem;
  padding: 2rem 1.4rem;
  width: 100%;
}
.output__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
}
.output__label {
  color: var(--cyan-l-gray-2);
  display: flex;
  flex-direction: column;
}
.output__label span {
  color: var(--cyan-d-gray-2);
}
.output__amount {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--cyan-main);
}

.people__box {
  display: flex;
  justify-content: space-between;
}
.people__error {
  color: orangered;
}

.hidden {
  visibility: hidden;
}

.outlineRed {
  outline: 2px solid orangered;
  color: orangered;
}

.container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.logo {
  margin: 3.1rem auto;
}